body{
    background-image: url("/static/images/mudryk.png");
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
}

@font-face {
    font-family:League;
    src: url("/static/fonts/LeagueSpartan-Bold.otf");
}

.chelsea_logo{
    margin-left: 3%;
    margin-top: 1%;
}

.chelsea-vid{
    position: relative;
    width: 300px; 
    height: auto; 
    border: 2px solid rgba(0, 226, 173, 0);
    border-radius: 12px;
    box-shadow: 0 0 100px black;
}

.chelsea-video { 
    position: absolute;
    margin-left: 79%;
    margin-top: 22%;
    text-decoration: underline;
}

nav{
    position: absolute;
    left: 70%;
    top: 3%;
}

nav button{
    margin-right: 10px;
    background-color: transparent;
    border:none;
    color: white;
    font-family: League;
    
}

ion-icon{
    color: white;
}